<template>
  <div class="tabs">
    <el-tag
      v-for="(item, index) in tags"
      :key="item.path"
      :closable="item.name !== 'home'"
      :effect="$route.name === item.name ? 'dark' : 'plain'"
      @click="ChangeMenu(item)"
      @close="handleClose(item, index)"
      size="small"
    >
      {{ item.label }}
    </el-tag>
  </div>
</template>
<script>
//功能：可以让多个页面进行展示
import { mapState, mapMutations } from "vuex";
export default {
  name: "CommonTag",
  data() {
    return {};
  },
  computed: {
    //把想要获得的数据放到mapState中
    ...mapState({
      tags: (state) => state.tab.tabsList,
    }),
  },
  methods: {
    ...mapMutations(["closeTag"]),
    //点击tag跳转功能
    ChangeMenu(item) {
      this.$router.push({ name: item.name });
    },
    //点击tag删除的功能
    handleClose(item, index) {
      //调用store中的mutations方法有this.$store.commit,还可以用辅助函数mapMutations
      this.closeTag(item);
      const length = this.tags.length;
      //删除之后的跳转逻辑
      //非激活的页面
      if (item.name !== this.$route.name) {
        return;
      }
      //表示的是删除的最后一页
      if (index === length) {
        this.$router.push({
          name: this.tags[index - 1].name,
        });
      } else {
        this.$router.push({
          name: this.tags[index].name,
        });
      }
    },
  },
};
</script>
<style lang="less" scoped>
.tabs {
  padding: 20px;
  .el-tag {
    margin-right: 15px;
    cursor: pointer;
  }
}
</style>
